
@font-face {
  font-family: 'Proxima Nova';
  src: url(static/fonts/proximanova.0b420ba.ttf);
}
body {
  margin: 0;
  padding: 0;
}
#app {
  font-family: "Proxima Nova", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#app h1 {
    font-weight: normal;
}
#app h2, #app h3 {
    font-weight: bold;
}
#app h1, #app h2, #app h3, #app h4 {
    color: #3d3d3d;
    margin: 0;
    padding: 0;
}
#app p {
    color: #41676c;
}
#app a, #app a:visited, #app a:hover {
    color: #fff;
}

#Nav {
  background-color: #1d4a53;
  height: 50px;
  line-height: 50px;
}
#Nav img {
    width: 120px;
    margin: 0 0 -5px 20px;
}
#Nav a {
    text-decoration: none;
}
#Nav a .fa-caret-down {
      margin-left: 20px;
      font-size: 1em;
}
#Nav ul {
    list-style-type: none;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    padding: 0;
    margin: 0 0 0 50px;
}
#Nav ul li {
      margin: 0 10px;
}
#Nav ul li a {
        font-size: 0.9em;
}
#Nav ul li a i {
          margin-right: 5px;
}
#Nav ul li a img {
          width: 25px;
}
#Nav ul li .selected {
        background-color: #29606a;
        padding: 5px 15px;
        border: 1px solid #29606a;
        border-radius: 5px;
}
@media (max-width: 1023px) {
#Nav {
    visibility: hidden;
    height: 60px;
}
#Nav ul {
      display: none;
}
}

#NewInput .outer-frame {
  background: #f9fbfb;
  padding: 20px 40px 60px 40px;
}
#NewInput .outer-frame h1 {
    font-size: 30px;
    margin-bottom: 20px;
}
#NewInput .outer-frame .button-container {
    margin: 20px 0;
    padding-bottom: 40px;
}
#NewInput .outer-frame .button-container button {
      height: 35px;
      padding: 0 25px;
      border-radius: 3px;
      font-family: 'Proxima Nova', sans-serif;
      font-weight: bold;
      font-size: 13px;
}
#NewInput .outer-frame .button-container .save-button {
      background: #3d8da6;
      border: 2px solid #3d8da6;
      color: #fff;
      float: left;
}
#NewInput .outer-frame .button-container .cancel-button, #NewInput .outer-frame .button-container .delete-button {
      float: right;
}
#NewInput .outer-frame .button-container .cancel-button {
      margin-right: 30px;
      border: 2px solid #d8e6e7;
}
#NewInput .outer-frame .button-container .delete-button {
      border: 2px solid #b72f26;
      background: #b72f26;
      color: #fff;
}
#NewInput .outer-frame .inner {
    border: 2px solid #bcd1d3;
    border-radius: 5px;
    background: #fff;
    height: 75vh;
    min-height: 540px;
}
#NewInput .outer-frame .inner input {
