      border-radius: 5px;
      height: 30px;
}
#NewInput .outer-frame .inner .field-types-container {
      background-color: #eef5f5;
      float: left;
      height: 75vh;
      min-height: 540px;
      overflow-y: scroll;
}
#NewInput .outer-frame .inner .field-types-container .field-types-inner {
        padding: 20px 25px 20px 20px;
}
#NewInput .outer-frame .inner .field-types-container .field-types {
