}
#NewInput .outer-frame .inner .field-types-container h4 {
        margin-bottom: 10px;
        font-size: 14px;
}
#NewInput .outer-frame .inner .field-types-container input {
        width: 100%;
}
#NewInput .outer-frame .inner .field-types-container ul {
        list-style-type: none;
        margin: 0;
        padding: 0;
}
#NewInput .outer-frame .inner .field-types-container ul li {
