}
#NewInput .outer-frame .inner .field-types-container ul li.selected h2, #NewInput .outer-frame .inner .field-types-container ul li.selected h3 {
            color: #f7fbfc;
}
#NewInput .outer-frame .inner .field-types-container ul li.selected p, #NewInput .outer-frame .inner .field-types-container ul li.selected i {
            color: #b3dbe1;
}
#NewInput .outer-frame .inner .placeholder-text p {
      text-align: center;
      font-size: 18px;
      margin-top: 20%;
}
#NewInput .outer-frame .inner .inputs-container {
      padding-top: 20px;
}
#NewInput .outer-frame .inner .inputs-container input {
