}
#NewInput .outer-frame .inner .inputs-container h2 {
        font-size: 20px;
        margin-bottom: 10px;
}
#NewInput .outer-frame .inner .inputs-container h4 {
        font-size: 14px;
        margin: 5px 0 5px 0;
}
#NewInput .outer-frame .inner .inputs-container .margin {
        margin-top: 40px;
}
#NewInput .outer-frame .inner .inputs-container p {
        font-size: 12px;
        margin-top: 5px;
}
#NewInput .outer-frame .tag-row h3 {
    margin: 0 0 15px 0;
}
#NewInput .outer-frame .tag-row h4 {
    margin: 10px 0;
}
#NewInput .outer-frame .tag-row button {
    background: #eef5f5;
    color: #41676c;
    border: 2px solid #d0e1e3;
    border-radius: 5px;
    font-family: 'Proxima Nova', sans-serif;
    font-weight: bold;
    font-size: 12px;
    padding: 8px 15px;
    margin: 3px 5px;
}
#NewInput .outer-frame .tag-row button:focus {
