}
#NewInput .outer-frame .tag-row button.active {
    color: #eef5f5;
    background-color: #41676c;
    border-color: #367f95;
}
#NewInput .outer-frame .tag-row ul {
    margin: 0;
    padding: 0;
    list-style: none;
}
#NewInput .outer-frame .tag-row ul li {
      font-size: 12px;
      line-height: 1.5;
