      color: #41676c;
}
#NewInput .outer-frame .field-groups {
    background: #f9fbfb;
    border: 2px solid #d0e1e3;
    border-radius: 5px;
    width: 80%;
    padding: 5%;
    margin-top: 20px;
    min-height: 430px;
    position: relative;
}
#NewInput .outer-frame .field-groups input {
      width: 98%;
      padding-left: 5px;
      font-family: 'Proxima Nova', sans-serif;
}
#NewInput .outer-frame .field-groups ul {
      list-style-type: none;
      margin: 0 0 15px 0;
      padding: 0;
      height: 250px;
      overflow-x: scroll;
}
#NewInput .outer-frame .field-groups ul li {
        font-size: 13px;
        font-weight: bold;
        color: #3d3d3d;
        background: #fff;
        border: 2px solid #d0e1e3;
        border-radius: 5px;
        padding: 10px 15px;
        margin: 15px 0;
}
#NewInput .outer-frame .field-groups ul li.active {
