}
#NewInput .outer-frame .field-groups button {
      border: 2px solid #d8e6e7;
      background: #fff;
      height: 35px;
      border-radius: 3px;
      font-family: 'Proxima Nova', sans-serif;
      font-weight: bold;
      font-size: 12px;
}
#NewInput .outer-frame .field-groups .add-field-group {
