        padding: 10px !important;
}
.outer-frame .inner .field-types-container .field-types-inner ul li i {
          font-size: 10px;
}
.outer-frame .inner .field-types-container .field-types-inner ul li h2 {
          font-size: 14px !important;
}
.outer-frame .inner .field-types-container .field-types-inner ul li h3 {
          font-size: 12px !important;
}
.outer-frame .inner .field-types-container .field-types-inner ul li p {
          font-size: 10px !important;
}
}
.site-menu, .toggle-overlay-label {
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}
.site-navbar {
  font-size: 14px;
  height: 60px;
  left: 20px;
  position: absolute;
  right: 20px;
  top: 20px;
  float: right;
  z-index: 100;
}
.site-navbar label {
    padding-left: 45px;
    padding-top: 4px;
    font-size: 16px;
    color: #41676c;
}
.site-menu {
  background: #eef5f5;
  bottom: 0;
  font-size: 14px;
  height: 100%;
  left: -300px;
  list-style: none;
  margin: 0;
  padding: 0;
  padding-top: 40px;
  position: fixed;
  top: 0;
  width: 300px;
}
.toggle-overlay-checkbox {
  display: none;
}
.toggle-overlay-label {
  bottom: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  right: 0;
  top: 0;
  z-index: -1;
}
.toggle-menu-checkbox:checked ~ .toggle-overlay-label {
  background: rgba(0, 0, 0, 0.5);
  opacity: 1;
  pointer-events: auto;
  z-index: 1;
}
.toggle-menu-checkbox,
.toggle-menu-label {
  display: none;
}
.toggle-menu-checkbox:checked ~ .site-menu {
  left: 0;
  z-index: 2;
  box-shadow: 4px 0 16px -4px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 4px 0 16px -4px rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 4px 0 16px -4px rgba(0, 0, 0, 0.2);
}
.toggle-overlay-checkbox:checked ~ .site-menu {
  left: -300px;
}
.toggle-menu-label {
  cursor: pointer;
  display: block;
  padding: 0 0 0 20px;
  position: absolute;
  left: 0px;
  top: 0px;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}
.toggle-menu-label:before {
    background: #41676c;
    content: "";
    height: 4px;
    position: absolute;
    left: 0;
    top: 1px;
    width: 2.2em;
    box-shadow: 0 10px 0 0 #41676c, 0 20px 0 0 #41676c;
    -moz-box-shadow: 0 10px 0 0 #41676c, 0 20px 0 0 #41676c;
    -webkit-box-shadow: 0 10px 0 0 #41676c, 0 20px 0 0 #41676c;
}
.toggle-menu-label:after {
    color: #41676c;
    content: "";
    display: inline-block;
    line-height: 1;
    margin-top: -9px;
    padding-right: 1.4em;
    text-align: center;
    text-transform: uppercase;
    vertical-align: middle;
    border-radius: 2px 2px 2px 2px;
}
